.story-check {
  order: 4;
  background-color: var(--basic-grey);
  margin-bottom: 33px;

  @media (min-width: $tablet-width) {
    order: 6;
    background-color: var(--basic-white);
    margin-bottom: 21px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1/3;
  }
}

.story-check__form {
  padding: 31px 0 0 0;
}

.story-check__fieldset {
  padding: 0 5px;
  border: none;

  @media (min-width: $tablet-width) {
    border: 2px solid var(--special-grey-border);
    border-radius: 1px;
    padding: 0;
  }
}

.story-check__headline {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: var(--basic-pink);
  text-transform: uppercase;
  text-align: center;
  margin: 0 16px 44px 1px;

  @media (min-width: $tablet-width) {
    margin: 0 16px 47px 1px;
  }
}

.story-check__list {
  font-size: 18px;
  font-weight: 400;
  color: var(--special-blue);
  margin: 0;
  padding: 0;

  @media (min-width: $tablet-width) {
    margin: 0 0 0 50px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 165px;
  }
}

.story-check__list:last-child {
  margin-bottom: 49px;

  @media (min-width: $desktop-width) {
    margin-bottom: 5px;
  }
}

.story-check__item {
  position: relative;
  margin: 0 0 52px 45px;

  @media (min-width: $tablet-width) {
    margin: 0 44px 52px 64px;
  }
}

.story-check__item::marker {
  font-size: 0;
}
